ze:cover;display:flex;flex-direction:column;color:white}.Content .hero-image .hero-image-inner{width:100%;height:100%;display:flex;flex-direction:column}.Content .hero-image .hero-image-inner h1{margin-top:64px;text-shadow:2px 2px 10px black}.Content .hero-image .hero-image-inner *{align-self:center}.Content li{padding-bottom:24px}.GetInvolved .summary{text-align:center;padding-bottom:26px}.GetInvolved .summary h1{margin-top:22px}.GetInvolved .summary p{padding-bottom:4px;font-size:18px}.GetInvolved .hero-image{min-height:160px}.GetInvolved .hero-image-inner{justify-content:center}.Resources .hero-image{min-height:160px}.flex-row{display:flex;flex-direction:row}.flex-column{display:flex;flex-direction:column}.bold{font-weight:bold}@media (max-width: 930px){.sm-col{flex-direction:column}}.AboutUs li{padding-bottom:unset}.AboutUs a{text-decoration:none;color:#0000EE}.AboutUs .hero-image-inner p{text-shadow:2px 2px 10px black;font-size:18px;text-align:center}.ResourceInfo{flex-basis:50%;flex-grow:1;flex-shrink:1;padding-bottom:24px}.ResourceInfo a{text-decoration:none;color:#0000EE}.ResourceInfo .resource-info-inner{display:flex}.ResourceInfo .icon-holder{font-size:36px;padding-right:8px}.ResourceInfo .icon-holder svg{color:white;box-shadow:5px 5px 15px 5px rgba(0,0,0,0.05);background-color:#EC1F27;background-color:var(--color-red);padding:8px;border-radius:50%}.ResourceInfo .text-holder{display:flex;flex-direction:column}.ResourceInfo .text-holder span{flex-grow:1}.ResourceInfo .text-holder .title{font-weight:bold;padding-bottom:8px;font-size:18px}

